@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2:wght@500;700&display=swap);
*{box-sizing:border-box}html{font-family:'Baloo Bhaijaan 2', cursive}

.Tabs__nav{display:inline-block;list-style:none}.Tab{display:inline-block}.Tab__link{padding:5px 10px;border:1px solid transparent;border-bottom:1px solid #000;text-decoration:none;color:blue;transition:color, border-color, 0.4s}.Tab__link:hover{color:#000;border-color:#000}.Tab__link--selected{border-color:#000;border-bottom-color:transparent;color:#000}.Tab__link--selected:hover{border-bottom-color:transparent}

